"use client";

import { useEffect, useState } from "react";
import { LexicalComposer } from "@lexical/react/LexicalComposer";
import { RichTextPlugin } from "@lexical/react/LexicalRichTextPlugin";
import { ContentEditable } from "@lexical/react/LexicalContentEditable";
import { HistoryPlugin } from "@lexical/react/LexicalHistoryPlugin";
import { OnChangePlugin } from "@lexical/react/LexicalOnChangePlugin";
import { AutoFocusPlugin } from "@lexical/react/LexicalAutoFocusPlugin";
import { MathJax } from "better-react-mathjax";

const theme = {
  paragraph: "mb-2 text-lg",
};

export default function LexicalEditor({ value, onChange, placeholder }) {
  return (
    <LexicalComposer
      initialConfig={{
        theme,
        onError: (error) => console.error(error),
      }}
    >
      <div className="border p-4 rounded-lg shadow">
        <RichTextPlugin
          contentEditable={
            <ContentEditable className="w-full p-2 min-h-[150px] border rounded bg-white text-gray-900" />
          }
          placeholder={<div className="text-gray-400">{placeholder}</div>}
        />
        <HistoryPlugin />
        <OnChangePlugin
          onChange={(editorState) => {
            editorState.read(() => {
              const json = JSON.stringify(editorState.toJSON());
              onChange(json); // Save as JSON
            });
          }}
        />
        <AutoFocusPlugin />
      </div>
      <div className="mt-2 p-2 bg-gray-100 rounded">
        <strong>Preview:</strong>
        <div className="p-2 text-gray-700">
          <MathJax>{value}</MathJax>
        </div>
      </div>
    </LexicalComposer>
  );
}
